.creating-wrapper {
  grid-template-columns: repeat(6, 1fr);
}
@media (max-width: 1400px) {
  .creating-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 768px) {
  .creating-wrapper {
    grid-template-columns: 1fr;
  }
}
.creating-wrapper__item {
  background: var(--white-background);
  border: 2px solid var(--white-background);
  backdrop-filter: blur(36px);
  grid-column: span 2;
}
.creating-wrapper__item img {
  width: 100%;
  max-height: 208px;
  object-fit: cover;
}
@media (max-width: 1400px) {
  .creating-wrapper__item img {
    height: 208px;
  }
}
.creating-wrapper__item:nth-child(n+4):nth-child(-n+5) {
  grid-column: span 3;
}
@media (max-width: 1400px) {
  .creating-wrapper__item {
    grid-column: span 1 !important;
  }
  .creating-wrapper__item:nth-last-child(1):nth-child(odd) {
    grid-column: span 2 !important;
  }
}
@media (max-width: 768px) {
  .creating-wrapper__item {
    grid-column: span 2 !important;
  }
}